<template>
  <div class="user-container">
    <div class="user-wrapper clearfix" >
         <div class="left-side" ref="leftSide">
            <LoginRegisterSide></LoginRegisterSide>
         </div>
         <div class="right-side">
             <router-view></router-view>
         </div>
    </div>
  </div>
</template>

<script>
import LoginRegisterSide from '@/components/TheLoginRegisterSide.vue'
export default {
  name: 'personCenter',
  mounted () {
    this.resizeUser('/register', 640)
    this.resizeUser('/forgetPassword', 482)
    this.resizeUser('/setPassword', 492)
  },
  methods: {
    resizeUser (path, height) {
      if (this.$route.path.includes(path)) {
        this.$refs.leftSide.style.height = height + 'px'
      }
    },
    watchResizeUser (to, from, path, height, originHeight) {
      if (to.path.includes(path)) {
        this.$refs.leftSide.style.height = height + 'px'
      } else if (from.path.includes(path)) {
        this.$refs.leftSide.style.height = originHeight + 'px'
      }
    }
  },
  components: {
    LoginRegisterSide
  },
  watch: {
    '$route' (to, from) {
      if (to.path.includes('/register')) {
        this.$refs.leftSide.style.height = 640 + 'px'
      } else if (from.path.includes('/register')) {
        this.$refs.leftSide.style.height = 460 + 'px'
      }
      if (to.path.includes('/forgetPassword')) {
        this.$refs.leftSide.style.height = 482 + 'px'
      } else if (from.path.includes('/forgetPassword')) {
        this.$refs.leftSide.style.height = 460 + 'px'
      }
      if (to.path.includes('/setPassword')) {
        this.$refs.leftSide.style.height = 492 + 'px'
      } else if (from.path.includes('/forgetPassword')) {
        this.$refs.leftSide.style.height = 460 + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .user-container{
    padding-bottom: 116px;
    .user-wrapper{
      background: #fff;
      width:900px;
      margin:0 auto;
      margin-top:90px;
      box-shadow: -12px 12px 32px 12px rgba(37,229,222,0.15);
      .left-side{
        float: left;
        width:360px;
        height:460px;
      }
      .right-side{
        background: #fff;
        width:440px;
        /*height:410px;*/
        float: right;
        padding:50px 50px 0;

      }

    }
  }

</style>
